section {
    height: 100vh;
}

.work__filters {
    display: flex;
    align-items: center;
    column-gap: 1.875rem;
    margin-bottom: 2.5rem;
}

.work__item {
    cursor: pointer;
    font-weight: var(--font-bold);
    transition: .3s;
}

.work__item:hover {
    color: var(--first-color);
}

.work__container {
    grid-template-columns: repeat(3, 1fr);
    gap: 1.875rem;
}

.work__card {
    position: relative;
    overflow: hidden;
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
}

.work__mask {
    background: #6c6ce5;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
}

.work__card:hover .work__mask {
    opacity: 0.9;
    transition: .3s;
}

.work__category {
    color: #fff;
    background-color: var(--first-color);
    border-bottom-left-radius: 0.98rem;
    border-bottom-right-radius: 0.98rem;
    position: absolute;
    top: 0;
    left: 1.5rem;
    font-size: var(--small-font-size);
    display: inline-block;
    padding: 0.19rem 0.625rem;
    transform: translateY(-40px);
    transition: .3s;
}

.work__title {
    color: #fff;
    font-size: var(--h3-font-size);
    margin: 0 0 0 0.98rem;
    padding: 0 1.25rem;
    top: 3.75rem;
    transform: translateY(30px);
    opacity: 0;
}

.work__button {
    color: #fff;
    position: absolute;
    bottom: 1.5rem;
    left: 1.5rem;
    font-size: var(--h3-font-size);
    display: block;
    background-color: #ffd15c;
    height: 40px;
    width: 40px;
    cursor: pointer;
    border: 50%;
    text-align: center;
    line-height: 42px;
    transition: .3s;
    opacity: 0;
    border-radius: 100%;
}

.work__card:hover .work__button {
    opacity: 1;
}

.work__card:hover .work__category,
.work__card:hover .work__title {
    opacity: 1;
    transform: translateY(0);
}

@media (max-width: 768px) {
    .work__button {
        bottom: 1rem;
        left: 1rem;
        font-size: var(--h5-font-size);
        display: block;
        background-color: #ffd15c;
        height: 20px;
        width: 20px;
        cursor: pointer;
        border: 50%;
        text-align: center;
        line-height: 22px;
        transition: .3s;
        opacity: 0;
        border-radius: 100%;
    }
    .work__title {
        color: #fff;
        font-size: 10px;
        margin: 0 0 0 0.98rem;
        padding: 0 1.25rem;
        top: 2.75rem;
        transform: translateY(30px);
    }
}