    /* ====GOOGLE Fonts ====== */
    
    @import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;1,500;1,600&family=Rubik:wght@300;400;500;600;700;800&display=swap');
    /* ====Variables CSS ====== */
    
     :root {
        /*=colors=*/
        --first-color: hsl(353, 100%, 65%);
        --title-color: hsl(244, 24%, 26%);
        --text-color: hsl(244, 16%, 43%);
        --body-color: hsl(258, 60%, 98%);
        --container-color: #fff;
        /*=Font and typography=*/
        --body-font: 'Rubik', sans-serif;
        --h1-font-size: 2.25rem;
        --h2-font-size: 1.5rem;
        --h3-font-size: 1.25rem;
        --normal-font-size: 1rem;
        --small-font-size: 0.875rem;
        --smaller-font-size: 0.81rem;
        /*=Font Weight=*/
        --font-medium: 500;
        --font-semibold: 600;
        --font-bold: 700;
        /*=Box Shadow=*/
        --shadow: 0px 5px 20px 0px rgb(69 67 96 /10%);
        /*=Border Radius=*/
        --border-radius: 20px;
    }
    
    [data-theme="dark"] {
        --first-color: hsl(353, 100%, 65%);
        --title-color: #fff;
        --text-color: #fff;
        --body-color: #3d3e51;
        --container-color: #fff;
        --logo-color: #fff;
    }
    /*=Responsive typography=*/
    
    @media screen and (max-width: 1024px) {
         :root {
            --h1-font-size: 1.75rem;
            --h2-font-size: 1.25rem;
            --h3-font-size: 1rem;
            --normal-font-size: 0.938rem;
            --small-font-size: 0.813rem;
            --smaller-font-size: 0.75rem;
        }
    }
    
    @media (max-width: 768px) {
         :root {
            --h1-font-size: 1.25rem;
            --h2-font-size: 1rem;
            --h3-font-size: 0.8rem;
            --normal-font-size: 0.738rem;
            --small-font-size: 0.613rem;
            --smaller-font-size: 0.55rem;
        }
    }
    /*= BAse =*/
    
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        scroll-behavior: smooth;
    }
    
    html {
        scroll-behavior: smooth;
    }
    
    body,
    button,
    input,
    textarea {
        font-family: var(--body-font);
        font-size: var(--normal-font-size);
    }
    
    body {
        background-color: var(--body-color);
        line-height: 1.7;
        color: var(--text-color);
    }
    
    h1,
    h2,
    h3 {
        color: var(--text-color);
        font-weight: var(--font-semibold);
        line-height: 1.2;
    }
    
    ul {
        list-style: none;
    }
    
    a {
        text-decoration: none;
    }
    
    button {
        cursor: pointer;
        border: none;
        outline: none;
    }
    
    img {
        max-width: 100%;
        height: auto;
        vertical-align: middle;
    }
    /*=CSS CLASSES=*/
    
    .container {
        max-width: 1080px;
        padding-left: 15px;
        padding-right: 15px;
        margin: 0 auto;
    }
    
    .grid {
        display: grid;
    }
    
    .section {
        padding-top: 7rem;
        padding-bottom: 2rem;
    }
    
    .section-title {
        font-size: var(--h1-font-size);
        margin-left: 0.875 rem;
        font-weight: var(--font-bold);
        position: relative;
        margin-bottom: 3.75rem;
    }
    
    .section-title::before {
        content: '';
        background: url(./assets/dots-bg.svg);
        height: 2.25rem;
        width: 2.25 rem;
        position: absolute;
        left: -0.875;
        top: -0.875;
    }
    
    .btn {
        padding: 0.75rem 2rem;
        line-height: 1;
        border-radius: 1.875rem;
        box-shadow: 0 0 1px rgb(0, 0, 0/0%);
        border: 1px solid transparent;
        color: #fff;
        background-color: var(--first-color);
        font-weight: var(--font-bold);
    }
    
    .btn:hover {
        background-color: #ff6600;
        /* Change to the desired hover color */
        transform: scale(1.05);
        /* Scale the button slightly on hover */
    }
    
    @media (max-width: 768px) {
        .btn {
            padding: 0.35rem 1rem;
        }
    }